<template><section><h1>TileMapServiceImageryProvider</h1>
<p>The <code v-pre>vc-provider-imagery-tile-mapservice</code> component is used to load tiled imagery that generated by MapTiler, GDAL2Tiles, etc.</p>
<h2>Example</h2>
<h3>Load an imagerylayer with TileMapServiceImageryProvider</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
          <vc-provider-imagery-tile-mapservice :url="url" :rectangle="rectangle" :maximumLevel="4" @readyPromise="imageryReady"></vc-provider-imagery-tile-mapservice>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>alpha</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>brightness</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>contrast</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
      </div>
    </div>
  </template>
  
</doc-preview>
