<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td><code v-pre>'.'</code></td>
<td><code v-pre>optional</code> Path to image tiles on server.</td>
</tr>
<tr>
<td>fileExtension</td>
<td>String</td>
<td><code v-pre>'png'</code></td>
<td><code v-pre>optional</code> The file extension for images on the server.</td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code> A credit for the data source, which is displayed on the canvas.</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> The minimum level-of-detail supported by the imagery provider. Take care when specifying this that the number of tiles at the minimum level is small, such as four or less. A larger number is likely to result in rendering problems.</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The maximum level-of-detail supported by the imagery provider, or undefined if there is no limit.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The rectangle, in radians, covered by the image.</td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The tiling scheme specifying how the ellipsoidal surface is broken into tiles. If this parameter is not provided, a WebMercatorTilingScheme is used.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid. If the tilingScheme is specified, this parameter is ignored and the tiling scheme's ellipsoid is used instead. If neither parameter is specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code> Pixel width of image tiles.</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code> Pixel height of image tiles.</td>
</tr>
<tr>
<td>flipXY</td>
<td>Boolean</td>
<td></td>
<td><code v-pre>optional</code> Older versions of gdal2tiles.py flipped X and Y values in tilemapresource.xml. Specifying this option will do the same, allowing for loading of these incorrect tilesets.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/TileMapServiceImageryProvider.html">TileMapServiceImageryProvider</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Triggers when the imagery provider encounters an asynchronous error.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>Triggers when the provider is ready for use.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          alpha: 1,
          brightness: 1,
          contrast: 1,
          url: './statics/SampleData/images/cesium_maptiler/Cesium_Logo_Color',
          rectangle: { west: -120, south: 20, east: -60, north: 40 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.viewer = viewer
          // ...
        },
        imageryReady(imageryProvider) {
          this.viewer.camera.flyTo({ destination: imageryProvider.rectangle })
        }
      }
    }
  </script>